import Vue from 'vue'
import Router from 'vue-router'
import home from '@/layout/home'
import subHome from '@/layout/subHome'
import store from '../store';
/* 
  meta:{
    hideLeft: true,  //隐藏左侧菜单
    isSubmenu: true, //开启左侧二级菜单
  }

*/
//修改原型对象中的push方法
const originalPush = Router.prototype.push
Router.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}
Vue.use(Router)
let publicRoutes = [{
    path: '/login',
    name: 'login',
    component: () => import('@/view/login')
  },
  {
    path: '/',
    name: '',
    component: home,
    redirect: '/First/page1',
  }, {
    path: '/First',
    name: 'First',
    component: home,
    icon: '首页',
    activeIcon: '首页1',
    redirect: '/First/page1',
    children: [{
      path: '/First/page1',
      name: 'Firstpage1',
      icon: '首页',
      activeIcon: '首页1',
      components: {
        default: () => import('@/view/First/page1.vue'),
      },
    }, {
      path: '/First/page2',
      name: 'Firstpage2',
      icon: '首页',
      activeIcon: '首页1',
      component: subHome,
      meta: {
        isSubmenu: true, //开启左侧二级菜单
      },
      children: [{
        path: '/First/page2/page1',
        name: '2-1',
        icon: '首页',
        activeIcon: '首页1',
        components: {
          default: () => import('@/view/First/page2/page1.vue'),
        },
      }, {
        path: '/First/page2/page2',
        name: '2-2',
        icon: '首页',
        activeIcon: '首页1',
        components: {
          default: () => import('@/view/First/page2/page2.vue'),
        },
      }],
    }, {
      path: '/First/page3',
      name: 'Firstpage3',
      icon: '首页',
      activeIcon: '首页1',
      components: {
        default: () => import('@/view/First/page3.vue'),
      },
    }, {
      path: '/First/page4',
      name: 'Firstpage4',
      icon: '首页',
      activeIcon: '首页1',
      components: {
        default: () => import('@/view/First/page4.vue'),
      },
    }]
  }, {
    path: '/Second',
    name: 'Second',
    component: home,
    icon: '首页',
    activeIcon: '首页1',
    redirect: '/Second/page1',
    children: [{
      path: '/Second/page1',
      // alias: '/Second',
      name: 'Secondpage1',
      icon: '首页',
      activeIcon: '首页1',
      components: {
        default: () => import('@/view/Second/page1.vue'),
      },
    }, {
      path: '/Second/page2',
      name: 'Secondpage2',
      icon: '首页',
      activeIcon: '首页1',
      components: {
        default: () => import('@/view/Second/page2.vue'),
      }
    }, {
      path: '/Second/page2Dtl',
      name: 'Secondpage2Dtl',
      icon: '首页',
      activeIcon: '首页1',
      meta: {
        hideLeft: true
      },
      components: {
        default: () => import('@/view/Second/page2Dtl.vue'),
      }
    }]
  }, {
    path: '*',
    component: () => import('@/view/404')
  }
]


let router = new Router({
  routes: publicRoutes,
})

router.beforeEach((to, from, next) => {
  let token = store.getters.token
  if (!token && to.name !== 'login') {
    next({
      path: 'login'
    })
  } else {
    next()
  }
})

export default router
export {
  publicRoutes
}



