<template>
  <div
    style="background-color: rgb(41, 60, 85);height:100vh;border-right: 1px solid #fff;"
  >
    <div>
      <div
        @click="switchCollapse"
        class="plr20"
        style="height:60px;line-height:60px;"
      >
        <img
          :class="[isCollapse ? 'logoImg' : '']"
          src="@/assets/images/logo.jpg"
          alt="公司logo"
        />
        <div class="comName" v-show="!isCollapse">admin3</div>
      </div>
      <!-- <div class="collapse">
        <i
          v-if="isCollapse"
          @click="switchCollapse(false)"
          class="el-icon-s-unfold full"
        ></i>
        <i v-else @click="switchCollapse(true)" class="el-icon-s-fold full"></i>
      </div> -->
    </div>
    <div v-for="(item, idx) in leftlist" :key="idx">
      <el-menu
        :default-active="defaultActive"
        class="el-menu-vertical-demo"
        :collapse="isCollapse"
        background-color="#293c55"
        text-color="rgba(255,255,255,0.45)"
        active-text-color="#f9f9f9"
        router
      >
        <el-submenu v-if="item.meta && item.meta.isSubmenu" :index="item.path">
          <template slot="title">
            <svg-icon
              v-if="defaultActive == item.path"
              :icon-class="item.activeIcon"
            ></svg-icon>
            <svg-icon v-else :icon-class="item.icon"></svg-icon>
            <span v-show="!isCollapse"> {{ $t("router." + item.name) }}</span>
          </template>

          <el-menu-item
            v-for="(it, i) in item.children"
            :key="i"
            :index="it.path"
          >
            <svg-icon
              v-if="defaultActive == it.path"
              :icon-class="it.activeIcon"
            ></svg-icon>
            <svg-icon v-else :icon-class="it.icon"></svg-icon>
            <span v-show="!isCollapse">
              {{ $t("router." + it.name) }}</span
            ></el-menu-item
          >
        </el-submenu>

        <el-menu-item v-else :index="item.path" :key="item.path">
          <svg-icon
            v-if="defaultActive == item.path"
            :icon-class="item.activeIcon"
          ></svg-icon>
          <svg-icon v-else :icon-class="item.icon"></svg-icon>
          <span v-show="!isCollapse"> {{ $t("router." + item.name) }}</span>
        </el-menu-item>
      </el-menu>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    leftMenulist: {
      type: Array,
      required: true
    }
  },
  watch: {
    $route(to, from) {
      // path数组,用于刷新回显左侧菜单激活项
      let pathList = [];
      this.leftMenulist.forEach(item => {
        pathList.push(item.path);
        if (item.children) {
          item.children.forEach(it => {
            pathList.push(it.path);
          });
        }
      });
      if (pathList.indexOf(to.path) >= 0) {
        this.defaultActive = to.path;
        localStorage.setItem("defaultActive", to.path);
      }
    },
    leftMenulist(val) {
      this.leftlist = val;
    }
  },
  data() {
    return {
      isCollapse: false,
      leftlist: this.leftMenulist,
      defaultActive: this.$route.path
    };
  },
  methods: {
    switchCollapse() {
      this.isCollapse = !this.isCollapse;
    }
  },
  created() {
    this.defaultActive =
      localStorage.getItem("defaultActive") || "/First/page1";
  }
};
</script>
<style lang="less" scoped>
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;
}

img {
  width: 30px;
  height: 30px;
  float: left;
  margin-top: 16px;
}
.logoImg {
  width: 20px;
  height: 20px;
}
.comName {
  float: left;
  margin-left: 15px;
  line-height: 60px;
  color: #fff;
  font-size: 16px;
  font-weight: 600;
}
.plr20 {
  padding: 0 20px;
}
.collapse {
  display: inline-block;
  width: 100%;
  text-align: center;
  height: 44px;
  line-height: 44px;
  color: #fff;
  .full {
    width: 100%;
    height: 100%;
  }
}
/deep/ .el-menu-item-group__title {
  padding: 0px !important;
}
/deep/.el-menu-item.is-active {
  background: #e43c59 !important;
}
.el-menu {
  border: none;
}
</style>



