<template>
  <el-container>
    <!--左侧导航-->
    <el-aside width="auto">
      <left-nav :leftMenulist="leftlist"></left-nav>
    </el-aside>
    <el-container>
      <!--头部-->
      <el-header height="auto" style="padding:0">
        <top-nav :topNavList="topNav" :topNavActive="topNavState"></top-nav>
      </el-header>
      <!--右侧内容区-->
      <el-main style="height:70vh">
        <transition name="fade" mode="out-in">
          <router-view></router-view>
        </transition>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
import LeftNav from "./components/leftNav.vue";
import topNav from "./components/topNav.vue";
export default {
  components: { topNav, LeftNav },
  data() {
    return {
      // 顶部菜单列表
      topNav: [],
      // 当前激活的顶部菜单
      topNavState: "",
      // 左侧菜单列表
      leftlist: []
    };
  },
  watch: {
    $route: function(to, from) {
      this.fetchNavData();
    }
  },
  methods: {
    // 获取顶部菜单
    getTopNav() {
      let routers = this.$router.options.routes;
      this.topNav = routers.filter((item, idx) => {
        if (item.children) {
          return item;
        }
      });
    },
    // 初始化顶部菜单激活项
    fetchNavData() {
      let cur_path = "/" + this.$route.path.split("/")[1], //当前一级菜单
        routers = this.$router.options.routes; // 获取路由对象
      routers.forEach((element, index) => {
        if (element.path == cur_path) {
          this.topNavState = element.path;
          this.setLeftNav(element.children);
        }
      });
    },
    // 设置左侧菜单
    setLeftNav(list) {
      list.map((item, idx) => {
        if (item.meta && item.meta.hideLeft) {
          list.splice(idx, 1);
        }
        return item;
      });
      this.leftlist = list;
    }
  },
  created() {
    this.getTopNav();
    this.fetchNavData();
  }
};
</script>
<style lang="less" scoped></style>



