<template>
  <div>
    <div class="content">
      <div class="content_item1">
        <el-menu
          :default-active="topNavState"
          class="el-menu-demo"
          mode="horizontal"
          background-color="#293c55"
          text-color="rgba(255,255,255,0.45)"
          active-text-color="#f9f9f9"
          router
        >
          <el-menu-item
            v-for="(item, index) in topNav"
            :key="index"
            :index="item.path"
          >
            <svg-icon
              v-if="item.path == topNavState"
              :icon-class="item.activeIcon"
            ></svg-icon>
            <svg-icon v-else :icon-class="item.icon"></svg-icon>
            {{ $t("router." + item.name) }}
          </el-menu-item>
        </el-menu>
      </div>
      <div class="content_item2 plr8" @click="fullScreen">
        <svg-icon style="margin-right:10px" icon-class="全屏"></svg-icon>
      </div>
      <div class="logOut">
        <el-dropdown trigger="click" @command="changeLanguage">
          <span class="el-dropdown-link">
            {{ language }}<i class="el-icon-arrow-down el-icon--right"></i>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="zh">{{
              $t("language.zh")
            }}</el-dropdown-item>
            <el-dropdown-item command="en">{{
              $t("language.en")
            }}</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
      <div class="logOut">
        <!--信息 -->
        <el-dropdown
          class="plr8 mr20"
          placement="bottom"
          @command="handleCommand"
          trigger="click"
        >
          <span class="el-dropdown-link">
            <i c·lass="el-icon-user-solid"></i>
            {{ token }}
            <i class="el-icon-arrow-down el-icon--right"></i>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="logOut">退出登录</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </div>
    <div class="topNav_tags">
      <Tags></Tags>
    </div>
    <div class="topNav_breadcrumb">
      <Breadcrumb />
    </div>
  </div>
</template>

<script>
import Breadcrumb from "./breadcrumb";
import Tags from "./tags.vue";
export default {
  components: {
    Breadcrumb,
    Tags
  },
  props: {
    topNavList: {
      type: Array,
      required: true
    },
    topNavActive: {
      type: String,
      required: true,
      default: "/First"
    }
  },
  watch: {
    topNavList(val) {
      this.topNav = val;
    },
    topNavActive(val) {
      this.topNavState = val;
    }
  },
  data() {
    return {
      topNav: this.topNavList,
      topNavState: this.topNavActive,
      isfullScreen: true,
      language: "中文",
      token: this.$store.getters.token
    };
  },
  methods: {
    // 全屏
    fullScreen() {
      if (this.isfullScreen) {
        var docElm = document.documentElement;
        if (docElm.requestFullscreen) {
          docElm.requestFullscreen();
        } else if (docElm.mozRequestFullScreen) {
          docElm.mozRequestFullScreen();
        } else if (docElm.webkitRequestFullScreen) {
          docElm.webkitRequestFullScreen();
        } else if (elem.msRequestFullscreen) {
          elem.msRequestFullscreen();
        }
        this.isfullScreen = false;
      } else {
        if (document.exitFullscreen) {
          document.exitFullscreen();
        } else if (document.mozCancelFullScreen) {
          document.mozCancelFullScreen();
        } else if (document.webkitCancelFullScreen) {
          document.webkitCancelFullScreen();
        } else if (document.msExitFullscreen) {
          document.msExitFullscreen();
        }
        this.isfullScreen = true;
      }
    },
    // 改变语言
    changeLanguage(command) {
      switch (command) {
        case "zh":
          this.language = this.$t("language.zh");
          this.$i18n.locale = "zh";
          break;
        case "en":
          this.language = this.$t("language.en");
          this.$i18n.locale = "en";
          break;
        default:
          this.language = this.$t("language.zh");
          this.$i18n.locale = "zh";
          break;
      }
    },
    // 菜单操作
    handleCommand(command) {
      switch (command) {
        case "logOut":
          this.$confirm("确定要退出吗?", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning"
          }).then(() => {
            this.$store.commit("logOut", "");
            this.$router.replace({
              path: "/login"
            });
          });

          break;
        default:
      }
    }
  }
};
</script>
<style lang="less" scoped>
.content {
  height: 60px;
  overflow: hidden;
  width: 100%;
  // min-width: 650px;
  display: flex;
}
.content_item1 {
  flex-grow: 1;
}
.content_item2 {
  background: #293c55;
  text-align: center;
  line-height: 60px;
  font-size: 25px;
  cursor: pointer;
}
.shopLogo {
  background: #293c55;
  width: 30px;
  height: 30px;
  margin-top: 15px;
}
.logOut {
  height: 60px;
  line-height: 60px;
  background: #293c55;
  cursor: pointer;
  i {
    color: #fff;
  }
  span {
    color: #fff;
  }
}
.plr8 {
  padding: 0 8px;
}
.topNav_breadcrumb {
  // box-shadow: 10px 1px 14px 2px #ccc;
  border-bottom: 1px solid rgb(236, 236, 236);
}
/deep/.el-menu--horizontal > .el-menu-item.is-active {
  border-bottom-color: #e43c59 !important;
  background: #0e151f !important;
}
.topNav_tags {
  background: #f6f8fc;
  height: 48px;
  line-height: 48px;
}
</style>



